/* Setting up fonts */
@font-face {
  font-family: "oswald";
  src: url("../fonts/oswald.woff") format("woff");
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-semibold.woff") format("woff");
  font-weight: 600;
}
@font-face {
  font-family: "nunito";
  src: url("../fonts/nunito-bold.woff") format("woff");
  font-weight: 700;
}
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: hsl(215, 92%, 15%);
  --secondary-color: hsl(41, 75%, 42%);
  --title-font: "oswald";
  --body-font: "nunito";
}

h1,
h2,
h3 {
  font-family: "oswald";
  letter-spacing: 1.4;
}

p,
li,
span {
  font-family: "nunito";
  letter-spacing: 1;
}

/* Remove default margin */
/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.6;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
.hero {
  height: 92vh;
  background: url("../img/hero-bg.jpg"), rgba(0, 0, 0, 0.7);
  background-blend-mode: darken;
  background-size: cover;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.hero .row {
  flex-direction: column;
  margin-bottom: 8em;
  text-align: center;
}
.hero .row .tag-line {
  font-size: 3em;
  color: var(--secondary-color);
  text-align: center;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
.hero .row .admission {
  margin-top: 2em;
  text-transform: uppercase;
  padding: 0.5em 1em;
  text-decoration: none;
  background: var(--secondary-color);
  border: none;
  border-radius: 5px;
}
.hero .row .admission a {
  font-family: var(--title-font);
  font-weight: 800;
  text-decoration: none;
  letter-spacing: 1.4;
  font-size: 1.3rem;
  color: var(--primary-color);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}

@media (min-width: 1000px) {
  .hero .row .tag-line {
    font-size: 4rem;
  }
}
.container {
  width: 90%;
  max-width: 1300px;
  margin: 0 auto;
}

.header {
  background-color: white;
  padding: 0.5em 0;
  text-align: center;
  position: relative;
  box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.25);
}
.header .nav-toggle {
  cursor: pointer;
  border: 0;
  width: 3em;
  height: 3em;
  padding: 0em;
  border-radius: 50%;
  transition: opacity 250ms ease;
  position: absolute;
  left: 0;
  background-color: white;
}
.header .nav-toggle::focus, .header .nav-toggle::hover {
  opacity: 0.75;
}
.header .nav-toggle .hamburger {
  width: 50%;
  position: relative;
  display: block;
  margin: 0 auto;
  height: 3px;
  border-radius: 8px;
  background: var(--primary-color);
}
.header .nav-toggle .hamburger::before, .header .nav-toggle .hamburger::after {
  display: block;
  margin: 0 auto;
  height: 3px;
  border-radius: 8px;
  content: "";
  background: var(--primary-color);
  transition: width 250ms;
}
.header .nav-toggle .hamburger::before {
  width: 120%;
  transform: translateY(-8px);
}
.header .nav-toggle .hamburger::after {
  width: 120%;
  transform: translateY(5px);
}
.header .nav-toggle .hamburger-open::after, .header .nav-toggle .hamburger-open::before {
  width: 80%;
  transition: width 250ms;
}
.header .logo .logo-image {
  width: 140px;
}
.header .nav {
  max-height: 0;
  overflow: hidden;
  transition: max-height 250ms;
}
.header .nav .nav-list {
  margin-top: 1em;
}
.header .nav .nav-list .nav-item {
  list-style-type: none;
  margin: 0.5em 0 0.5em 0;
}
.header .nav .nav-list .nav-item .nav-link {
  display: inline-block;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--primary-color);
  font-family: var(--title-font);
  font-weight: 700;
  font-size: 1.125rem;
  cursor: pointer;
}
.header .nav .nav-list .nav-item .nav-link:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: var(--primary-color);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.header .nav .nav-list .nav-item .nav-link:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

@media (min-width: 1000px) {
  .row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header .nav-toggle {
    visibility: hidden;
  }
  .header .nav {
    max-height: 100vh;
    overflow: auto;
  }
  .header .nav .nav-list {
    display: flex;
    margin: 0;
    align-items: center;
  }
  .header .nav .nav-list .nav-item {
    margin-left: 2em;
  }
}